.box {
  width: 100%;
  min-width: 450px;
  min-height: 90px;
  margin: 2px;
  margin-bottom: 0px;
  padding: 15px;
  flex-wrap: wrap;
  font-size: calc(9px + 1vmin);
}
.column{
  display: inline-block;
  padding: 3px;
  padding-left: 0;
}
.item{
  width: 100%;
  display: flex;
  margin-top: 10px;
  margin-bottom: 10px;
}
.label {
  margin-left: 10px;
  margin-right: 5px;
  line-height: 32px;
  width: 100px;
  min-width: 100px;
}
.input {
  width : 100%;
  min-width:80px;
  height: 100%;
  display: flex;
}
.root {
  display: flex;
  align-items: stretch;
  padding: 3px;
  flex-wrap: wrap;
}
.root>div>div{
  border-style: solid;
  border-radius: 8px;
  border-width: 2px;
  border-color: darkcyan;
  margin: 2px;
}
#r1c1 {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 65%;
}
#r1c2 {
  display: flex;
  width: 35%;
}
